

























import Vue from "vue";

export default Vue.extend({
  name: "TheSearchBar",
  components: {},
  data: () => {
    return {
      searchData: {
        type: "name",
        value: "",
      },
      items: [
        {
          text: "Name",
          value: "name",
          selected: true,
        },
        {
          text: "Identifier",
          value: "id",
        },
        {
          text: "Episode",
          value: "ep",
        },
      ],
    };
  },
  methods: {
    search() {
      if (this.searchData.value === "") {
        this.searchData.type = "name";
      }

      this.$store.commit("changeSearchData", this.searchData);
    },
  },
});
